import sun from "../assets/sun.svg";
import water from "../assets/water.svg";
function CareScale({ scaleValue, careType }) {
  function handleClick() {
    let myString = "";
    let quantity = "";
    let type = careType === "water" ? "d'arrosage" : "de lumiÃ¨re";
    switch (scaleValue) {
      case 1:
        quantity = "peu ";
        break;
      case 2:
        quantity = "modÃ©rement ";
        break;
      case 3:
        quantity = "beaucoup ";
        break;
      default:
        quantity = "Erreur ";
    }
    myString = "Cette plante requiert " + quantity + type;
    alert(myString);
  }
  const range = [1, 2, 3];
  const scaleType =
    careType === "water" ? (
      <img src={water} alt="water" />
    ) : (
      <img src={sun} alt="sun" />
    );
  return (
    <div onClick={handleClick}>
      {range.map((rangeElement) =>
        scaleValue >= rangeElement ? (
          <span key={rangeElement.toString()}>{scaleType}</span>
        ) : null
      )}
    </div>
  );
}

export default CareScale;
